export default {
	data() {
		return {
			system:this.systemInfo(),
			user:this.getUserInfo(),
			order:{},
			goods:{},
			shipArray:["顺丰","申通","韵达","圆通","中通","天天","邮政","德邦","中铁","韵达"],
			modalName:'',
			reason:''
		}
	},
	onLoad(option) {
		let code = option.code;
		this.postJson({
			path:"/order_detail.jsp",
			data:{
				code:code
			},
			success:(res)=>{
				if("ok" == res.data.status){
					this.order = res.data.data.order;
					this.goods = res.data.data.goods;
					this.goods.stateLabel = this.getStateLabel(this.goods.status);
					this.refund = res.data.data.refund;
					this.order.createTimeLabel = new Date(this.order.create_time).toLocaleDateString();
				}
			}
		});
	},
	methods: {
		getStateLabel:function(){
			if(this.refund && (this.order.status == 1 || this.order.status == 2)){
				return "退货申请中";
			}else{
				let statusLabelArray = ["待支付","待发货","待确认","已完成","已退货","已取消"];
				return statusLabelArray[this.order.status];
			}
		},
		orderCancel:function(){
			uni.showLoading({
				title:'加载中'
			});
			this.postJson({
				path:"/user_order_cancel.jsp",
				data:{
					code: this.order.code
				},
				success:(res) => {
					uni.hideLoading();
					if("ok" == res.data.status){
						uni.redirectTo({
							url:"/pages/order/order?code="+this.order.code
						});
					}
				}
			})
		},
		orderSellerCancel:function(){
			this.postJson({
				path:"/seller_order_cancel.jsp",
				data:{
					code: this.order.code
				},
				success:(res) => {
					if("ok" == res.data.status){
						uni.redirectTo({
							url:"/pages/order/order?code="+this.order.code
						});
					}
				}
			})
		},
		confirm:function(){
			uni.showLoading({
				title:'加载中'
			});
			this.postJson({
				path:"/user_order_confirm.jsp",
				data:{
					code: this.order.code
				},
				success:(res) => {
					uni.hideLoading();
					if("ok" == res.data.status){
						uni.redirectTo({
							url:"/pages/order/order?code="+this.order.code
						});
					}
				}
			});
		},
		contactBuyer:function(){
			uni.makePhoneCall({
				phoneNumber:this.order.phone
			});
		},
		contactSeller(){
			uni.showLoading({
				title:"加载中",
				mask:true
			});
			this.postJson({
				path:"/seller_user.jsp",
				data:{
					sellerUid:this.goods.seller
				},
				success:(res)=>{
					if("ok" == res.data.status){
						let seller = res.data.data;
						uni.makePhoneCall({
							phoneNumber:seller.phone
						});
						
					}else{
						uni.showToast({
							icon:'none',
							title:res.data.msg
						});
					}
				},
				complete:()=>{
					uni.hideLoading();
				}
				
			});
		},
		orderRefund(){
			if(!this.reason || this.reason.trim().length<=0){
				uni.showToast({
					icon:'none',
					title:"请输入退货理由"
				});
				return;
			}
			uni.showLoading({
				title:'加载中'
			});
			
			this.postJson({
				path:"/user_order_refund.jsp",
				data:{
					code: this.order.code,
					reason: this.reason
				},
				success:(res) => {
					uni.hideLoading();
					if("ok" == res.data.status){
						uni.redirectTo({
							url:"/pages/order/order?code="+this.order.code
						});
					}
				}
			});
		},
		
		refundPass(){
			
			uni.showLoading({
				title:'加载中'
			});
			
			this.postJson({
				path:"/seller_order_refund_pass.jsp",
				data:{
					code: this.order.code
				},
				success:(res) => {
					uni.hideLoading();
					if("ok" == res.data.status){
						uni.redirectTo({
							url:"/pages/order/order?code="+this.order.code
						});
					}
				}
			});
		},
		
		refundRefuse(){
			if(!this.reason || this.reason.trim().length<=0){
				uni.showToast({
					icon:'none',
					title:"请输入拒绝理由"
				});
				return;
			}
			uni.showLoading({
				title:'加载中'
			});
			
			this.postJson({
				path:"/seller_order_refund_refuse.jsp",
				data:{
					code: this.order.code,
					reason: this.reason
				},
				success:(res) => {
					uni.hideLoading();
					if("ok" == res.data.status){
						uni.redirectTo({
							url:"/pages/order/order?code="+this.order.code
						});
					}
				}
			});
		},
		showModal(e) {
			this.modalName = e.currentTarget.dataset.target
		},
		hideModal(e) {
			this.modalName = '';
		}
	}
}
